import { Component, OnInit, ViewChild, Inject } from '@angular/core';
import { CustomerService, UpholsteryService, ProjectService } from '@app/core';

import {
	Customer,
	Upholstery,
	Project,
	Acknowledgement,
	Address,
} from '@app/core/models';
import { MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { SearchListComponent } from '@app/shared/components/search-list/search-list.component';
import { FormControl, FormGroup, FormBuilder, Validators, FormArray, AbstractControl } from '@angular/forms';
import { ArrayValidators } from '@app/shared/validators/array-validators';
import { Item as QItem } from '@app/core/models/quotation';
import { ValidationMessages, ValidationMessage } from '@app/shared/components/add-order/add-order.component';


@Component({
    templateUrl: './acknowledgement-dialog.component.html',
    styleUrls: ['./acknowledgement-dialog.component.less'],
    providers: [Customer, Acknowledgement]
})
export class AcknowledgementDialogComponent implements OnInit {
    @ViewChild('addSearch')
    addSearch!: SearchListComponent;

    @ViewChild('customerSearch')
    customerSearch!: SearchListComponent;

    form: FormGroup = this.fb.group({
        customer: this.fb.group({
            id: [null],
            name: ['', Validators.required],
            telephone: [''],
            email: [null],
            tax_id: [''],
            addresses: this.fb.array([
                this.fb.group({
                    address1: [null],
                    city: [null],
                    territory: [null],
                    country: [null],
                    zipcode: [null]
                })
            ]),
        }),
        project: this.fb.group({
            id: [null],
            codename: [null],
        }),
        delivery_date: [null, Validators.required],
        discount: [''],
        second_discount: [''],
        vat: [7, [Validators.required, Validators.min(1)]],
        remarks: [''],
        items: this.fb.array([
        ], ArrayValidators.minLength(1))
    });
    validation_messages: ValidationMessages = {
        name: [
            { type: 'required', message: 'Customer name is required'}
        ],
        delivery_date: [
            { type: 'required', message: 'Delivery date is required' }
        ],
        vat: [
            { type: 'required', message: 'VAT is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ],
        items: [
            { type: 'minLength', message: 'At least one item is required' }
        ],
        quantity: [
            { type: 'required', message: 'A quantity is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ],
        price: [
            { type: 'required', message: 'A price is required' },
            { type: 'min', message: 'A value greater than 0 is required' }
        ]
    };

    private _progress_check = {
        customer: false,
        project: false,
    };

    constructor(public acknowledgement: Acknowledgement,
                public customer: Customer,
                public customerService: CustomerService,
                public upholstery: Upholstery,
                public upholsteryService: UpholsteryService,
                public project: Project,
                public projectService: ProjectService,
                private fb: FormBuilder,
                private snackBar: MatSnackBar,
                private dialogRef: MatDialogRef<AcknowledgementDialogComponent>,
                @Inject(MAT_DIALOG_DATA) private dialogData: {acknowledgement?: Acknowledgement}) {
        /*
        customer.addresses = [{
          address1: null,
          territory: null,
          city: null,
          country: null,
          zipcode: null
        }];
        */
        this.acknowledgement.items = [];
        this.acknowledgement.customer = customer;
        this.acknowledgement.customer.addresses = [new Address()];

        if (this.dialogData) {
            console.log(this.dialogData);

            if (this.dialogData.acknowledgement) {
                this.acknowledgement.get(this.dialogData.acknowledgement.id)
                                    .subscribe((a: Acknowledgement) => {
                                        this._hydrateAcknowledgement(a);
                                    });
            }
        }

    }


    ngOnInit() {
        if (this.dialogData) {
            console.log(this.dialogData);
            if (this.dialogData.acknowledgement) {
                this.acknowledgement.get(this.dialogData.acknowledgement.id)
                                    .subscribe((a: Acknowledgement) => {
                                        this._hydrateAcknowledgement(a);
                                    });
            }
        }
    }

    private _hydrateAcknowledgement(acknowledgement: Acknowledgement): void {

        // Patch general
        this.getFormControl('delivery_date').patchValue(acknowledgement.delivery_date);
        this.getFormControl('vat').patchValue(acknowledgement.vat);
        this.getFormControl('discount').patchValue(acknowledgement.discount);
        this.getFormControl('second_discount').patchValue(acknowledgement.second_discount);
        this.getFormControl('remarks').patchValue(acknowledgement.remarks);

        // Patch Customer
        this.getFormGroup('customer').patchValue(acknowledgement.customer);
        Object.assign(this.customer, acknowledgement.customer);
        this.customerSearch.autoControl.setValue(this.customer);

        // Patch Project
        if (acknowledgement.project) {
            this.getFormGroup('project').patchValue(acknowledgement.project);
            Object.assign(this.project, acknowledgement.project);
        }

        // Add Items
        if (acknowledgement.items) {
            const items = acknowledgement.items as QItem[];
            items.forEach((item: QItem) => {
                this.items.push(this.addQuotationItem(item));
            });
        }
    }

    public get addresses(): FormArray {
        return this.getFormGroup('customer').get('addresses') as FormArray;
    }

    public get items(): FormArray {
        return this.form.get('items') as FormArray;
    }

    public displayError(n: string | FormControl, t: string): boolean {
        const ctrl = typeof(n) === 'string' ? this.getFormControl(n) : n;

        return ctrl.invalid && ctrl.hasError(t);
    }

    public getErrorMessage<K extends keyof AcknowledgementDialogComponent['validation_messages']>(k: K, errorType: string): string {
        const validationMessages: ValidationMessage[] = this.validation_messages[k];
        const msg: ValidationMessage = validationMessages.find((val: ValidationMessage) => val.type === errorType)
                                       || {type: '', message: ''};
        return msg.message;
    }

    public setCustomer(customer: Customer | string): void {
        if (customer) {
            const c = typeof(customer) === 'string' ? {
                id: null as null,
                name: customer,
                email: '',
                telephone: '',
                tax_id: '',
                addresses: [{}]
            } : customer;
            const custControl = this.getFormGroup('customer');
            custControl.patchValue(c);

            this.addresses.controls[0].patchValue(c.addresses[0]);
        }

    }

    public setProject(project: Project | string): void {
        const p = typeof(project) === 'string' ? {
            id: null as null,
            codename: project
        } : project;
        const projectConstrol = this.getFormControl('project');
        projectConstrol.patchValue(p);
    }

    public addItem(val: Upholstery | string): void {
        let obj: FormGroup;
        obj = typeof(val) === 'string' ? this.addCustomItem(val) : this.addStandardItem(val);

        const fa: FormArray = this.items;
        fa.push(obj);

        this.addSearch.reset();
    }

    public removeItem(index: number): void {
        const fa: FormArray = this.items;
        fa.removeAt(index);
    }

    public create(): void {
        if (this.form.valid) {
            this.form.disable();
            this.form.enable();

            const formVal = this.form.value;
            Object.assign(this.acknowledgement, formVal);

            // Recreate Customer
            const c = new Customer(this.customerService);
            Object.assign(c, formVal.customer);
            this.acknowledgement.customer = c;

            this.acknowledgement.customer.save(() => {
                this._progress_check.customer = true;
                this._create();
            });

            // Recreate Project
            if (formVal.project.codename && formVal.project.codename !== '') {
                const p: Project = new Project(this.projectService);
                Object.assign(p, formVal.project);
                this.acknowledgement.project = p;

                if (!this.acknowledgement.project.id) {
                    this.acknowledgement.project.save(() => {
                        this._progress_check.project = true;
                        this._create();
                    });
                } else {
                    this._progress_check.project = true;
                    this._create();
                }
            } else {
                delete formVal.project;
                delete this.acknowledgement.project;
                this._progress_check.project = true;
            }

        }
    }

    protected _create() {
        if (this._progress_check.project && this._progress_check.customer) {
            this.form.disable();
            this.snackBar.open('Creating Acknowledgement...', 'Dismiss');
            this.acknowledgement.save((res: Acknowledgement) => {
                this.snackBar.open('Acknowledgement Created', 'Dismiss', {duration: 3000});
                this.form.reset();

                this.dialogRef.close(res);
            });
        }
    }   

    protected addStandardItem(r: Upholstery): FormGroup {
        const fg = this.fb.group({
            id: [r.id, Validators.required],
            description: [{value: r.description, disabled: r.id}, Validators.required],
            model: this.fb.group({
                id: [{value: r.model.id, disabled: true}],
                model: [{value: r.model.model, disabled: true}],
                name: [{value: r.model.name, disabled: true}]
            }),
            configuration: this.fb.group({
                id: [{value: r.configuration.id, disabled: true}],
                configuration: [{value: r.configuration.configuration, disabled: true}]
            }),
            price: [r.price, [Validators.required, Validators.min(1)]],
            quantity: [null, [Validators.required, Validators.min(1)]],
            width: [r.width],
            depth: [r.depth],
            height: [r.height],
            comments: [null],
            image: this.fb.group({
                id: [(r.image || {id: ''}).id],
                url: [(r.image || {url: ''}).url]
            })
        });

        return fg;
    }

    protected addCustomItem(description: string): FormGroup {
        const fg = this.fb.group({
            description: [description, Validators.required],
            price: ['', [Validators.required, , Validators.min(1)]],
            quantity: ['', [Validators.required, , Validators.min(1)]],
            width: [0],
            depth: [0],
            height: [0],
            comments: ['']
        });

        return fg;
    }

    protected addQuotationItem(item: QItem): FormGroup {
        const fg = this.fb.group({
            id: [item.product],
            description: [item.description, Validators.required],
            unit_price: [item.unit_price, [Validators.required, , Validators.min(1)]],
            quantity: [item.quantity, [Validators.required, , Validators.min(1)]],
            width: [item.width],
            depth: [item.depth],
            height: [item.height],
            comments: [item.comments],
            product: [item.product],
            image: this.fb.group({
                id: [(item.image || {id: ''}).id],
                url: [(item.image || {url: ''}).url]
            })
        });

        return fg;
    }

    protected getFormControl(controlName: string): AbstractControl {
        return this.form.get(controlName) || this.addFormGroup(controlName);
    }

    protected addFormControl(controlName: string): AbstractControl {
        const control: FormControl = this.fb.control(null);
        return this.form.registerControl(controlName, control);
    }

    protected getFormGroup(groupName: string): AbstractControl {
        return this.form.get(groupName) || this.addFormGroup(groupName);
    }

    protected addFormGroup(groupName: string): AbstractControl {
        const group: FormGroup = this.fb.group({});
        return this.form.registerControl(groupName, group);
    }
}



