import { Component, OnInit, Inject } from '@angular/core';
import { CustomerService, UpholsteryService, ProjectService } from '@app/core';
import {
	Customer,
	Acknowledgement,
	Upholstery,
	Project,
	Address,
} from '@app/core/models';
import { MatSnackBar, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { FormBuilder, Validators, FormGroup } from '@angular/forms';
import { ArrayValidators } from '@app/shared/validators/array-validators';
import { Quotation, Item as QItem } from '@app/core/models/quotation';
import { AddOrderComponent } from '@app/shared/components/add-order/add-order.component';


@Component({
    templateUrl: './add-acknowledgement.component.html',
    styleUrls: ['./add-acknowledgement.component.less'],
    providers: [Customer, Acknowledgement]
})
export class AddAcknowledgementComponent extends AddOrderComponent<Acknowledgement> implements OnInit {
    form: FormGroup = this.fb.group({
        customer: this.fb.group({
            id: [null],
            name: ['', Validators.required],
            telephone: ['', Validators.required],
            email: [null],
            tax_id: [''],
            addresses: this.fb.array([
                this.fb.group({
                    address1: [null],
                    city: [null],
                    territory: [null],
                    country: [null],
                    zipcode: [null]
                })
            ]),
        }),
        project: this.fb.group({
            id: [null],
            codename: [null],
        }),
        delivery_date: [null, Validators.required],
        discount: [''],
        second_discount: [''],
        vat: [7, [Validators.required, Validators.min(0)]],
        remarks: [''],
        items: this.fb.array([
        ], ArrayValidators.minLength(1))
    });
    

    protected _progress_check = {
        customer: false,
        project: false
    };

    constructor(public acknowledgement: Acknowledgement,
                public customer: Customer,
                public customerService: CustomerService,
                public upholstery: Upholstery,
                public upholsteryService: UpholsteryService,
                public project: Project,
                public projectService: ProjectService,
                protected fb: FormBuilder,
                protected snackBar: MatSnackBar,
                protected dialogRef: MatDialogRef<AddAcknowledgementComponent>,
                @Inject(MAT_DIALOG_DATA) private dialogData: {quotation: Quotation}) {

        super(acknowledgement,
            customer,
            customerService,
            upholstery,
            upholsteryService,
            project,
            projectService,
            fb,
            snackBar,
            dialogRef);
        /*
        customer.addresses = [{
          address1: null,
          territory: null,
          city: null,
          country: null,
          zipcode: null
        }];
        */
        this.acknowledgement.items = [];
        this.acknowledgement.customer = customer;
        this.acknowledgement.customer.addresses = [new Address()];

    }


    ngOnInit() {
        if (this.dialogData) {
            if (this.dialogData.quotation) {
                this._hydrateAcknowledgement(this.dialogData.quotation);
            }
        }
    }

    private _hydrateAcknowledgement(quotation: Quotation): void {
        // Patch general
        this.getFormControl('vat').patchValue(quotation.vat);
        this.getFormControl('discount').patchValue(quotation.discount);
        this.getFormControl('second_discount').patchValue(quotation.second_discount);
        this.getFormControl('remarks').patchValue(quotation.remarks);

        // Patch Customer
        this.getFormGroup('customer').patchValue(quotation.customer);
        Object.assign(this.customer, quotation.customer);

        // Patch Project
        if (quotation.project) {
            this.getFormGroup('project').patchValue(quotation.project);
            Object.assign(this.project, quotation.project);
        }

        // Add Items
        if (quotation.items) {
            const items = quotation.items as QItem[];
            items.forEach((item: QItem) => {
                this.items.push(this.addQuotationItem(item));
            });
        }
    }

    public create(): void {
        if (this.form.valid) {
            this.form.disable();
            this.form.enable();

            const formVal = this.form.value;
            Object.assign(this.acknowledgement, formVal);

            // Recreate Customer
            const c: Customer = new Customer(this.customerService);
            Object.assign(c, formVal.customer);
            this.acknowledgement.customer = c;

            if (this.acknowledgement.customer instanceof Customer) {
                this.acknowledgement.customer.save(() => {
                    this._progress_check.customer = true;
                    this._create();
                });
            } 

            // Recreate Project
            if (formVal.project.codename && formVal.project.codename !== '') {
                const p: Project = new Project(this.projectService);
                Object.assign(p, formVal.project);
                this.acknowledgement.project = p;

                if (!this.acknowledgement.project.id) {
                    this.acknowledgement.project.save(() => {
                        this._progress_check.project = true;
                        this._create();
                    });
                } else {
                    this._progress_check.project = true;
                    this._create();
                }
            } else {
                delete formVal.project;
                delete this.acknowledgement.project;
                this._progress_check.project = true;
            }

        }
    }

    protected _create() {
        if (this._progress_check.project && this._progress_check.customer) {
            this.form.disable();
            this.snackBar.open('Creating Acknowledgement...', 'Dismiss');
            this.acknowledgement.save((res: Acknowledgement) => {
                this.snackBar.open('Acknowledgement Created', 'Dismiss', {duration: 3000});
                this.form.reset();

                this.dialogRef.close(res);
            });
        }
    }

    protected addQuotationItem(item: QItem): FormGroup {
        const fg = this.fb.group({
            id: [item.product],
            description: [item.description, Validators.required],
            unit_price: [item.unit_price, [Validators.required, , Validators.min(1)]],
            quantity: [item.quantity, [Validators.required, , Validators.min(1)]],
            width: [item.width],
            depth: [item.depth],
            height: [item.height],
            comments: [item.comments],
            product: [item.product],
            image: this.fb.group({
                id: [(item.image || {id: ''}).id],
                url: [(item.image || {url: ''}).url]
            })
        });

        return fg;
    }
}



